
'use client';

import { useState, useTransition } from 'react';
import { useRouter } from 'next/navigation';
import { useAuth } from '@/contexts/AuthContext';
import { Toaster, toast } from 'react-hot-toast';
import { registrarMascota } from '@/app/actions/mascotasActions';
import { ShieldCheckIcon, ArrowLeftIcon } from '@heroicons/react/24/outline'; // Iconos de contorno
import { PawPrintIcon } from '@heroicons/react/24/solid'; // Iconos sólidos

export default function NuevaMascotaPage() {
  const { user, loading: authLoading } = useAuth();
  const router = useRouter();
  const [isPending, startTransition] = useTransition();

  const [formData, setFormData] = useState({
    nombre: '',
    especie: '',
    raza: '',
    fechaNacimiento: '',
    sexo: '',
    enAdopcion: false, // Nuevo campo para adopción
  });

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    setFormData(prev => ({ ...prev, [name]: type === 'checkbox' ? checked : value }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!user) {
        return toast.error("Debes iniciar sesión para registrar una mascota.");
    }

    startTransition(async () => {
        const result = await registrarMascota(user, formData);
        if (result.success) {
            toast.success('¡Mascota registrada con éxito!');
            router.push('/mascotas');
        } else {
            toast.error(result.error || "Hubo un problema al registrar la mascota.");
        }
    });
  };

  if (authLoading) {
      return (
          <div className="flex justify-center items-center h-screen bg-violet-100">
              <div className="text-center">
                  <PawPrintIcon className="animate-spin h-12 w-12 text-violet-600 mx-auto" />
                  <p className="text-violet-800 font-semibold mt-2">Cargando tu perfil...</p>
              </div>
          </div>
      );
  }

  return (
    <div className="min-h-screen bg-violet-200 flex flex-col justify-center py-12 sm:px-6 lg:px-8 font-sans">
      <Toaster position="top-center" toastOptions={{ duration: 4000 }} />
      <div className="sm:mx-auto sm:w-full sm:max-w-xl">
          <button onClick={() => router.back()} className="flex items-center gap-2 text-violet-800 hover:text-violet-900 font-semibold mb-4">
              <ArrowLeftIcon className="h-5 w-5" />
              Volver
          </button>
          <div className="bg-white/60 backdrop-blur-sm p-4 rounded-2xl shadow-lg">
              <div className="sm:mx-auto sm:w-full sm:max-w-md">
                <PawPrintIcon className="mx-auto h-12 w-auto text-violet-800" />
                <h2 className="mt-6 text-center text-3xl font-extrabold text-gray-900">Registrar Mascota</h2>
                <p className="mt-2 text-center text-sm text-gray-600">
                  Añade los datos de tu compañero fiel.
                </p>
              </div>

              <div className="mt-8 sm:mx-auto sm:w-full sm:max-w-md">
                <div className="bg-transparent py-8 px-4 sm:px-10">
                  <form onSubmit={handleSubmit} className="space-y-6">
                    {/* ... campos del formulario con mejor diseño ... */}
                    <div>
                      <label htmlFor="nombre" className="block text-sm font-medium text-gray-700">Nombre</label>
                      <input id="nombre" name="nombre" type="text" required value={formData.nombre} onChange={handleChange} className="input-style" />
                    </div>
                    <div>
                      <label htmlFor="especie" className="block text-sm font-medium text-gray-700">Especie</label>
                      <input id="especie" name="especie" type="text" required value={formData.especie} onChange={handleChange} className="input-style" placeholder="Ej: Perro, Gato" />
                    </div>
                    <div>
                      <label htmlFor="raza" className="block text-sm font-medium text-gray-700">Raza</label>
                      <input id="raza" name="raza" type="text" value={formData.raza} onChange={handleChange} className="input-style" placeholder="Ej: Labrador, Siames" />
                    </div>
                    <div>
                      <label htmlFor="fechaNacimiento" className="block text-sm font-medium text-gray-700">Fecha de Nacimiento</label>
                      <input id="fechaNacimiento" name="fechaNacimiento" type="date" required value={formData.fechaNacimiento} onChange={handleChange} className="input-style" />
                    </div>
                    <div>
                        <label htmlFor="sexo" className="block text-sm font-medium text-gray-700">Sexo</label>
                        <select id="sexo" name="sexo" required value={formData.sexo} onChange={handleChange} className="input-style">
                            <option value="">Selecciona uno...</option>
                            <option value="Macho">Macho</option>
                            <option value="Hembra">Hembra</option>
                        </select>
                    </div>
                    
                    {/* Interruptor para Adopción */}
                    <div className="flex items-center justify-between">
                        <span className="flex-grow flex flex-col">
                            <span className="text-sm font-medium text-gray-900">¿Poner en adopción?</span>
                            <span className="text-xs text-gray-500">Si marcas esto, tu mascota será visible en la sección pública de adopciones.</span>
                        </span>
                        <label htmlFor="enAdopcion" className="inline-flex relative items-center cursor-pointer">
                            <input type="checkbox" id="enAdopcion" name="enAdopcion" checked={formData.enAdopcion} onChange={handleChange} className="sr-only peer" />
                            <div className="w-11 h-6 bg-gray-200 rounded-full peer peer-focus:ring-4 peer-focus:ring-violet-300 peer-checked:after:translate-x-full peer-checked:after:border-white after:content-[''] after:absolute after:top-0.5 after:left-[2px] after:bg-white after:border-gray-300 after:border after:rounded-full after:h-5 after:w-5 after:transition-all peer-checked:bg-violet-600"></div>
                        </label>
                    </div>

                    <div className="pt-4">
                      <button type="submit" disabled={isPending} className="w-full flex justify-center py-3 px-4 border border-transparent rounded-full shadow-lg text-sm font-medium text-white bg-violet-700 hover:bg-violet-800 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-violet-500 disabled:opacity-60 transition-all duration-300 transform hover:scale-105">
                         {isPending ? 'Guardando...' : 'Registrar Mascota'}
                      </button>
                    </div>
                  </form>
                </div>
              </div>
          </div>
      </div>
    </div>
  );
}
